@import url('https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@300;400;500;600;700&display=swap');

body {
  background-color: #131A35;
  font-family: 'Space Grotesk', sans-serif;
}

input {
  border: none;
  appearance: none;
  -webkit-appearance: none;
  -ms-appearance: none;
  -moz-appearance: none;
  background: #f2f2f2;
  padding: 12px;
  border-radius: 3px;
  width: 250px;
  outline: none;
  font-size: 14px;

  &:focus::placeholder {
    color: transparent;
  }

  &::placeholder {
    color: #222;
    transition: color .3s ease;
  }
}

.page-content {
  padding-top: 4vw;
  padding-bottom: 25vw;
  min-height: 100%;
}

.gitBook {
  font-weight: 800;
  font-size: 0.8vw;
  color: #fff;
  display: flex;
  text-decoration: none;
  align-items: center;
  // margin-right: 3vw;
  border: 1px solid #5ac4be;
  padding: 10px;
  border-radius: 10px;
  max-width: 150px;
  display: flex;
  justify-content: center;

  flex: 1 1 auto;




  cursor: pointer;

  svg {
    margin-right: 15px;


  }

  &:hover {
    color: #5ac4be;

    svg {
      path {
        fill: #fff
      }
    }
  }
}

.cardContent {
  display: flex;
  align-items: center;
  justify-content: center;

  width: 90%;

  padding: 1px;
  background: linear-gradient(245.22deg, #da2eef 7.97%, #2b6aff 49.17%, #39d0d8 92.1%);
  border-radius: 4px;

  &__body {
    width: 100%;
    display: grid;
    grid-auto-rows: auto;
    grid-row-gap: 12px;
    row-gap: 12px;
    padding: 32px 24px;
    background-color: #131a35;
    opacity: .9;
    border-radius: 4px;
  }
}

.loader {
  display: flex;
  align-items: center;
  justify-content: center;

  margin-top: 2vw;
}

.back {
  position: absolute;
  top: -40px;
  left: 0;
  margin-bottom: 1vw;

  span {
    transition: .2s ease-in-out;
    cursor: pointer;

    &:hover {
      color: #5ac4be;
    }
  }

}

.blockDesc {
  margin-bottom: 1vw;

  &__upload {
    margin-top: 10px;
    font-size: 0.8vw;
  }

  & .littleText {
    font-size: 0.7vw;
    max-width: 50%;
    margin-top: 10px;
  }
}

.blockBtns {
  display: flex;
  align-items: center;

  width: 30vw;

  & .gitBook {
    &:first-child {
      margin-right: 15px;
    }
  }
}

.dropArea {
  margin-top: 20px;
  width: 30vw;
  height: 30vh;
  border: 2px dashed white;
  display: flex;
  align-items: center;
  justify-content: center;

  img {
    max-height: 100%;
  }
}

.videoPlayer {
  width: 100%;
  height: 100%;
}